import { _decorator, Component, Node, tween, Vec3 } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('logoAnim')
export class logoAnim extends Component {

    start() {

    	showEffect();
    }

    update(deltaTime: number) {
        
    }

    showEffect() {
        cc.tween(this.node)
	    .to(2, { scale: 2, position: cc.v2(10, 10), rotation: 90 })
	    .repeatForever()
	    .start()
    }
}

